import React, { Component } from "react";

class FunctionInTemplateComponent extends Component {
  constructor(props) {
    super(props);
    this.state = { user: { firstName: "Arnold", lastName: "Schwarzenegger" } };
  }
  render() {
    return <ng-function-in-template user={this.state.username}></ng-function-in-template>;
  }
}
export default FunctionInTemplateComponent;
